@import "./common/styles/sass/variables.scss";
@import "./common/styles/sass/mixins.scss";
* {
  box-sizing: border-box;
}
body {
  @include text($secondaryFont, 16px, 1.8, 400);
}
.App{
  >div{
    &:nth-child(n){
      background-color: $bgColor;

    }
    &:nth-child(2n){
      background-color: $bgSecondColor;

    }
  }
}